import { PageContent } from '@ra-lib/admin';
import config from 'src/commons/config-hoc';

export default config({
    path: '/auto-deps',
    connect: (state) => {
        return {
            name: state.demo.present.name,
            user: state.demo.present.user,
        };
    },
})(function AutoDeps(props) {
    const { name, user } = props;

    function handleClick() {
        props.action.demo.setName('是啊');
    }

    function handleClick2() {
        props.action.demo.getUser();
    }

    return (
        <PageContent>
            <div onClick={handleClick}>setName</div>
            <div onClick={handleClick2}>getUser</div>
            <div>{name}</div>
            <div>{JSON.stringify(user)}</div>
        </PageContent>
    );
});
